.page {
  margin-top: 20px;

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 48px;
    color: #0d0d0e;
    text-transform: uppercase;
    letter-spacing: .2px;
    text-align: center;
  }

}

.page-container {
  position: relative;
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.page-banner {
  position: relative;
  margin-top: 25px;
  height: 830px;
  background-repeat: repeat;
  background-image: url('../images/money/bg.png');
  width: 100%;

  &_2 {
    background-image: url('../images/time/bg.png');
    background-color: #3b3b3d;
  }

  &_3 {
    background-image: url('../images/resources/bg.png');
    background-color: #8033e0;
  }

  &_4 {
    background-image: url('../images/growth/bg.png');
    background-color: #6ed1cd;
  }

  &__text-box-1 {
    position: absolute;
    top: 126px;
    left: 50%;
    margin-left: -400px;
    width: 400px;
    background: #fff;

    &_1 {
      padding: 100px;
    }

    &_2 {
      padding: 100px 50px;
    }

    &_3 {
      padding: 60px 45px 80px;
      background: #3b3b3d;
      z-index: 1;
    }

    &_4 {
      padding: 100px 50px;
      z-index: 1;
    }

  }

  &__text-box-2 {
    position: absolute;
    top: 275px;
    left: 50%;
    margin-left: -120px;
    width: 620px;
    background: #fff;

    &_1 {
      padding: 60px;
    }

    &_2 {
      top: 300px;
      margin-left: -90px;
      width: 680px;
      padding: 60px 60px 60px 120px;
    }

  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 36px;
    color: #3b3b3d;
    line-height: 48px;
    text-transform: uppercase;
    letter-spacing: .2px;

    &_2 {
      color: #fff;
    }

  }

  &__description {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 24px;
    color: #3b3b3d;
    line-height: 36px;
    letter-spacing: .2px;
  }

}

.page-banner-money {
  position: relative;
  height: 690px;
  background: #6ed1cd;

  &__text-box-1 {
    position: absolute;
    top: 165px;
    left: 50%;
    margin-left: -475px;
    width: 345px;
    height: 326px;
    background: url("../images/money/bg-2.png");
  }

  &__text-box-2 {
    position: absolute;
    top: 165px;
    left: 50%;
    margin-left: -50px;
    width: 560px;
  }

  &__title {
    position: absolute;
    top: 140px;
    left: 90px;
    display: inline-block;
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 48px;
    color: #3b3b3d;
    text-transform: uppercase;
    letter-spacing: .2px;
    transform: rotate(-90deg);
  }

  &__description {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 24px;
    color: #fff;
    line-height: 36px;
    letter-spacing: .2px;
  }

}

.page-banner-time {
  position: relative;
  height: 690px;
  overflow: hidden;

  &:before {
    content: '';
    position: absolute;
    width: 50%;
    height: inherit;
    background: #6ed1cd;
  }

  &__circle-wrap {
    padding: 100px 0;
  }

  &__circle {
    width: 490px;
    height: 490px;
    border-radius: 50%;
    background-color: #3b3b3d;
    background-image: url("../images/time/bg-2.png");
  }

  &__description-wrap {
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 90px;
  }

  &__description {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 24px;
    color: #3b3b3d;
    line-height: 36px;
    letter-spacing: .2px;
  }

}

.page-banner-resources {
  position: relative;
  height: 690px;
  background: #3b3b3d;

  display: flex;
  align-items: center;
  justify-content: center;

  &__description {
    max-width: 840px;
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 24px;
    color: #fff;
    line-height: 36px;
    letter-spacing: .2px;
  }

}

.page-banner-growth {
  position: relative;
  height: 690px;
  background: #8033e0;

  &__text-box-1 {
    position: absolute;
    top: 190px;
    left: 50%;
    margin-left: -475px;
    width: 290px;
    height: 290px;
    background: url("../images/money/bg-2.png");
  }

  &__text-box-2 {
    position: absolute;
    top: 190px;
    left: 50%;
    margin-left: -100px;
    width: 590px;
    height: 290px;
    display: flex;
    align-items: center;
  }

  &__title {
    position: absolute;
    top: 140px;
    left: 90px;
    display: inline-block;
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 48px;
    color: #3b3b3d;
    text-transform: uppercase;
    letter-spacing: .2px;
    transform: rotate(-90deg);
  }

  &__description {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 24px;
    color: #fff;
    line-height: 36px;
    letter-spacing: .2px;
  }

}

.page-service {
  padding: 30px 0 60px;
  $g: &;

  &_2 {
    padding-top: 75px;

    #{$g}__title {
      color: #3b3b3d;
    }

  }

  &_3 {

    #{$g}__title {
      color: #8033e0;
    }

  }

  &_4 {
    padding-top: 75px;

    #{$g}__title {
      color: #8033e0;
    }

  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 36px;
    color: #6ed1cd;
    line-height: 48px;
    text-transform: uppercase;
    letter-spacing: .2px;
  }

}

.page-service-list {
  display: flex;
  flex-wrap: wrap;
  margin: 40px -55px 0;

  $g: &;

  &_2 {

    #{$g}__link {
      border-color: #3b3b3d;

      &:hover {
        background: #3b3b3d;
      }

    }

  }

  &_3 {

    #{$g}__link {
      border-color: #8033e0;

      &:hover {
        background: #8033e0;
      }

    }

  }

  &__item {
    width: 50%;
    padding: 0 55px;
  }

  &__link {
    display: block;
    min-height: 240px;
    margin: -1px 0;
    padding: 45px 80px 45px 45px;
    text-decoration: none;
    border-color: #6ed1cd;
    border-style: solid none solid none;
    border-width: 2px;
    transition: all .2s ease-in-out;

    &:hover {
      background: #6ed1cd;

      & h3 {
        color: #fff;
      }

      & ul li {
        color: #fff;
      }

    }

    & h3 {
      display: block;
      font-family: $font-family-one;
      font-weight: 800;
      font-size: 24px;
      color: #272727;
      line-height: 24px;
      letter-spacing: .2px;
      transition: all .2s ease-in-out;
    }

    & ul {
      margin: 20px 0 0 0;
      padding: 0;
      list-style: none;

      & li {
        font-family: $font-family-one;
        font-weight: 300;
        font-size: 14px;
        color: #272727;
        line-height: 24px;
        letter-spacing: .2px;
        transition: all .2s ease-in-out;
      }
    }

  }

}

.page-important {
  padding: 60px 0;
  background: #e0e6ee;

  $g: &;

  &_2 {
    background: #3b3b3d;

    #{$g}__title {
      color: #6ed1cd;
    }

    #{$g}__description {
      color: #6ed1cd;
    }

  }

  &_3 {
    background: #3b3b3d;

    #{$g}__title {
      color: #fff;
    }

    #{$g}__description {
      color: #fff;
    }

  }

  &_4 {
    background: #6ed1cd;

    #{$g}__title {
      color: #3b3b3d;
    }

    #{$g}__description {
      color: #3b3b3d;
    }

  }

  &__text-box {
    padding-right: 40px;

    &_pl {
      padding-left: 40px;
    }

  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 36px;
    color: #3b3b3d;
    line-height: 48px;
    text-transform: uppercase;
    letter-spacing: .2px;
  }

  &__description {
    margin: 30px 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 14px;
    color: #3b3b3d;
    line-height: 24px;
    letter-spacing: .2px;
  }

}

.page-important-two {
  position: relative;
  padding-bottom: 100px;
  height: 100%;

  $g: &;

  &:before {
    content: '';
    position: absolute;
    width: 50%;
    height: inherit;
    background: #3b3b3d;
  }

  &_2 {

    &:before {
      background: #6ed1cd;
    }

  }

  &_3 {

    &:before {
      background: #e0e6ee;
    }

    #{$g}__title {
      color: #3b3b3d;
    }

    #{$g}__description {
      color: #3b3b3d;
    }

  }

  &_4 {

    &:before {
      background: #8033e0;
    }

  }

  &__text-wrap {
    display: flex;
    align-items: center;
  }

  &__text-box {
    padding-right: 40px;
  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 36px;
    color: #fff;
    line-height: 48px;
    text-transform: uppercase;
    letter-spacing: .2px;
  }

  &__description {
    margin: 30px 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 14px;
    color: #fff;
    line-height: 24px;
    letter-spacing: .2px;
  }

  &__form-wrap {
    padding: 100px 0 0 100px;
  }

}
